.creat-button{
  width: 100%;
  border-top: 1px solid #e9e9e9;
  padding: 16px 16px 0px;
  background: #fff;
  text-align: right;
}
.evaluate-detail{
  table{
    width: 100%;
    tbody{ 
      border: 1px solid #ccc;
      padding: 15px !important;
      display: block;
      border-radius: 10px;
      tr{
        td{
          text-align: center;
          width: 10% !important;
          padding: 5px;
          textarea{
            min-height: 32px;
            height: fit-content;
            
          }
        }
      }
    }
}
}
.button-modal {
  margin: 0 5px;
  cursor: pointer;
  i {
      font-weight: 500 !important;
      font-size: 20px !important;
      color: #228BF5 !important;
  }
}
.ant-form-item-children span.date {
  display: block;
  
}
.addition-box{
  .ant-calendar-picker{
      width: 100% !important;
  }
}
.table-sanitation-processes{
  width: 100% !important;
}
.addition-box{
  .ant-calendar-picker{
      width: 48% !important;
      padding-right: 5px;
  }
}